        </div>

        <!-- 时间戳 premiereAt-->
        <div class="descText">
          {{ filmDescInfo.premiereAt | dateFilter }}
        </div>

        <!-- 国家 -->
        <div class="descText">
          {{ filmDescInfo.nation }} | {{ filmDescInfo.runtime }} 分钟
        </div>

        <!-- 详情信息 -->
        <!-- 为了隐藏显示详情问题, 注意动态&静态class可以共存 -->
        <div
          class="descText"
          style="line-height: 1.0714rem"
          :class="isHidden ? 'hidden' : ''"
        >
          {{ filmDescInfo.synopsis }}
        </div>

        <!-- 隐藏显示详情的箭头 -->
        <div style="text-align: center">
          <i
            class="iconfont"
            @click="isHidden = !isHidden"
            :class="isHidden ? 'icon-arrow-down' : 'icon-arrow-up'"
          ></i>
        </div>

        <!-- 演职人员 -->
