              <div style="text-align: center; font-size: 13px">
                {{ item.role }}
              </div>
            </desc-swiper-item>
          </desc-swiper>
        </div>

        <!-- 剧照 -->
        <div>
          <div>剧照</div>
          <desc-swiper :preview="2.5" name="photos">
            <desc-swiper-item
              v-for="(item, index) in filmDescInfo.photos"
              :key="index"
            >
              <div
                :style="{ backgroundImage: 'url(' + item + ')' }"
                class="avatar"
                @click="handlePreview(index)"
              ></div>
            </desc-swiper-item>
          </desc-swiper>
        </div>
      </div>
    </div>
    <!-- 全部数据 -->
    <!-- <div>
      {{ filmDescInfo }}
