</template>

<script>
// import axios from 'axios'
import http from '@/util/http'
import moment from 'moment' // unix时间转换成时间戳
import Vue from 'vue'
import descSwiper from '@/components/description/DescSwiper'
import descSwiperItem from '@/components/description/DescSwiperItem'
import descHeader from '@/components/description/DescHeader'
// vant image preview
import { ImagePreview } from 'vant'
import { Toast } from 'vant'
// import mixin obj
import SHTAB from '@/util/shtab'
// impot vuex mapMutations
import { mapMutations } from 'vuex'

// 通过指令可以拿到底层原生DOM节点
// 显示隐藏Header
Vue.directive('scroll', {
  inserted(el, binding) {
    el.style.display = 'none'
    window.onscroll = () => {
      if (document.documentElement.scrollTop || document.body > binding.value) {
        el.style.display = 'block'
      } else {
        el.style.display = 'none'
      }
    }
  },
  unbind() {
    window.onscroll = null
  }
})

moment.locale('zh-cn') // 中文时间格式
// console.log(moment().format('YYYY/MM/DD'))
// 过滤器
Vue.filter('dateFilter', (dt) => {
  return moment(dt * 1000).format('YYYY/MM/DD')
})

export default {
  // use mixin obj
  mixins: [SHTAB],
  // !复杂的底层DOM操作， 封装到自定义指令中
  //
  // mounted() {
  //   window.onscroll = () => {
  //     console.log('scroll')
  //     if (document.documentElement.scrollTop > 50) {
  //       console.log('显示')
  //       this.isShow = true
  //     } else {
  //       console.log('隐藏')
  //       this.isShow = false
  //     }
  //   }
  // },
  // destroyed() {
  //   window.onscroll = null
  // },
  data() {
    return {
      // 应该使用 {}, 而不是 [], 因为后期数据类型可能会发生更改
      filmDescInfo: {},
      isHidden: true
    }
  },
  components: {
    descSwiper,
    descSwiperItem,
    descHeader,
    [ImagePreview.Component.name]: ImagePreview.Component
  },
  //   编程式导航
  methods: {
    // use vuex mapMutations
    ...mapMutations(['show', 'hide']),
    handlePreview(index) {
      ImagePreview({
        images: this.filmDescInfo.photos,
        startPosition: index,
        closeable: true,
        closeIconPosition: 'top-left'
      })
    }
  },
  created() {
    // 进入详情页面, 立即显示 "加载中...", 利用 Vant 组件库实现
    // Toast.loading({ message: '加载中...', forbidClick: true, duration: 4000 })

    // console.log('created', this.$route.params.id)

    // 1. 需要对axios进行封装
    // axios({
    //   url: `https://m.maizuo.com/gateway?filmId=${this.$route.params.id}&k=4424388`,
    //   headers: {
    //     'X-Client-Info':
    //       '{"a":"3000","ch":"1002","v":"5.2.0","e":"1656764823272000278855681","bc":"110100"}',
    //     'X-Host': 'mall.film-ticket.film.info'
    //   }
    // }).then((res) => {
    //   this.filmDescInfo = res.data.data.film
    // })

    // 2. 方法一: 通过函数封装
    // http.httpForDesc(this.$route.params.id).then((res) => {
    //   this.filmDescInfo = res.data.data.film
    // })

    // 3. 方法二: axios 自带方法, 自定义创建新实例
    http({
      url: `https://m.maizuo.com/gateway?filmId=${this.$route.params.id}&k=1190794`,
      headers: {
        'X-Host': 'mall.film-ticket.film.info'
      }
    }).then((res) => {
      this.filmDescInfo = res.data.data.film
      console.log(res)

      // // 数据加载结束, 关闭 vant '加载中...'; 但是不推荐这样做, 应当在 axios 的拦截中实现
      // Toast.clear()
    })
  }
}
</script>

