  computed: mapState(['cinemaList', 'cityId', 'cityName']),
  methods: {
    // vuex的mapActions & mapMutations
    ...mapActions(['getCinemaData']),
    ...mapMutations(['clearCinemaList']),
    handleLeft() {
      this.$router.push('/city')
      // 每次点击, vuex提交清空cinemaList的缓存的操作
      // this.$store.commit('clearCinemaList')
      // mapMutations的使用方法:
      this.clearCinemaList()
    },
    handleRight() {
      this.$router.push('/cinemas/search')
    }
  },
  mounted() {
    // 动态计算高度
    this.height =
