    // if (this.$store.state.cinemaList.length === 0) {
    if (this.cinemaList.length === 0) {
      // 异步请求
      // this.$store.dispatch('getCinemaData', this.$store.state.cityId)
      // 需要在异步请求结束的时候， 加载 betterScroll

      // this.$store.dispatch('getCinemaData', this.$store.state.cityId)
      // this.$store.dispatch('getCinemaData', this.cityId)
      // mapActions 方法映射后
      this.getCinemaData(this.cityId).then((res) => {
        // console.log('异步请求结束, 此处进行 betterScroll 初始化')
        this.$nextTick(() => {
          new BetterScroll('.box', {
            scrollbar: {
              fade: true
            }
          })
        })
      })
    } else {
      console.log('影院使用缓存')
      this.$nextTick(() => {
        new BetterScroll('.box', {
          scrollbar: {
            fade: true
          }
        })
      })
    }
    // axios 请求, 为了异步数据请求实现数据缓存, 迁移到vuex中的actions中去做
    // http({
    //   url: `https://m.maizuo.com/gateway?cityId=${this.$store.state.cityId}&ticketFlag=1&k=7583052`,
    //   headers: { 'X-Host': 'mall.film-ticket.cinema.list' }
    // }).then((res) => {
    //   this.cinemalist = res.data.data.cinemas
    //   console.log(this.cinemalist)
    //   // 引用 better-scroll
    //   // 3 个, better scroll 还没上树
    //   // new BetterScroll('.box')
    //   // console.log(console.log(document.getElementsByTagName('li').length))
    //   // update 之后才上树
    //   this.$nextTick(() => {
    //     new BetterScroll('.box', {
    //       scrollbar: {
    //         fade: true
    //       }
    //     })
    //     // console.log(console.log(document.getElementsByTagName('li').length))
    //   })
    // })
  }
}
