<template>
  <div>
    <!-- vant search 组件 -->
    <!-- v-model 双向绑定输入值 -->
    <van-search
      v-model="value"
      show-action
      placeholder="请输入影院关键词"
      @search="onSearch"
      @cancel="onCancel"
    />

    <!-- vant 影院列表 -->
    <!-- 只在搜索框有输入值的时候显示列表 -->
    <ul v-if="value">
      <!-- 通过vuex中的异步数据获取 -->
      <li v-for="item in computedCinemaList" :key="item.cinemaId">
        <!-- 左半边 -->
        <div class="left">
          <div clss="cinema_name">
            {{ item.name }}
          </div>
          <div class="cinema_text">{{ item.address }}</div>
        </div>
        <!-- 有半边 -->
        <div class="cinema_name">
          <div style="color: green">¥{{ item.lowPrice / 100 }}起</div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { ref } from 'vue'
import { Toast } from 'vant'
import { mapState, mapActions, mapMutations } from 'vuex'
// import mixin obj
import SHTAB from '@/util/shtab'

export default {
  // use mixin obj
  mixins: [SHTAB],
  data() {
    return {
      value: ''
    }
  },
  // 计算属性
  computed: {
    // vuex mapState
    ...mapState(['cinemaList', 'cityId']),
    // 通过filter实现模糊查询
    computedCinemaList() {
      // console.log(this.$store.state.cinemaList)
      // return this.$store.state.cinemaList.filter(
      // return this.cinemaList.filter(
      return this.cinemaList.filter(
        (item) =>
          item.name.toUpperCase().includes(this.value.toUpperCase()) || // 基于用户输入进行filter模糊查询, 注意全部转为大写,
          item.address.toUpperCase().includes(this.value.toUpperCase()) // 以及包括对输入地址的模糊查询
      )
    }
  },
  methods: {
    // mapMutations
    ...mapMutations(['hide', 'show']),
    // vuex mapActions
    ...mapActions(['getCinemaData']),
    onSearch() {
      Toast({ duration: 100, message: '搜索' })
    },
    onCancel() {
      Toast({ duration: 100, message: '取消' })
      // router 返回上级页面
      this.$router.back()
    }
  },
  // vant search 组件
  setup() {
    const value = ref('')
    const onSearch = (val) => Toast(val)
    const onCancel = () => Toast('取消')
    return { value, onSearch, onCancel }
  },
  mounted() {
    // 分发, 与影院列表一样获取异步获取的缓存数据
    // if (this.$store.state.cinemaList.length === 0) {
    if (this.cinemaList.length === 0) {
      // 异步请求
      // this.$store.dispatch('getCinemaData', this.$store.state.cityId)
      // this.$store.dispatch('getCinemaData', this.cityId)
      this.getCinemaData(this.cityId)
    } else {
      console.log('search 使用缓存')
    }
  }
}
</script>

