ul {
  padding-left: 0;
}
li {
  padding: 1.0714rem;
  display: flex;
  justify-content: space-between;
  .left {
    width: 212px;
  }
  .cinema_name {
    font-size: 15px;
  }
  .cinema_text {
    color: #797d82;
    font-size: 12px;
    margin-top: 0.3571rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
.box {
  height: 44.1429rem;
  overflow: hidden;
  position: relative; // 修正滚动条位置
}
</style>
