import http from '@/util/http'
import Vue from 'vue'
import Vuex from 'vuex'
// 解决 vuex 持久化问题
import createPersistedState from "vuex-persistedstate";



Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [createPersistedState(
    // 通过reducer剔除不需要的数据
    {
      reducer: (state) => {
        return { // 只保留需要的数据
          cityId: state.cityId,
          cityName: state.cityName
        }
      }
    })
  ],
  state: {
    cityId: '110100',
    cityName: '北京',
    cinemaList: [],
    isTabbarShow: true
  },
  getters: {
  },
  mutations: {
    changeCityId(state, cityId) {
      state.cityId = cityId
    },
    changeCityName(state, cityName) {
      state.cityName = cityName
    },
    setCinemaData(state, data) {
      state.cinemaList = data
    },
    clearCinemaList(state) {
      state.cinemaList = []
    },
    show(state) {
      state.isTabbarShow = true
    },
    hide(state) {
      state.isTabbarShow = false
    }
  },
  actions: {
    getCinemaData(store, cityId) { // action 中是store, 不是state
      return http({
        url: `https://m.maizuo.com/gateway?cityId=${cityId}&ticketFlag=1&k=7583052`,
        headers: { 'X-Host': 'mall.film-ticket.cinema.list' }
      }).then((res) => {
        // console.log(res.data.data.cinemas)
        // 存储
        store.commit("setCinemaData", res.data.data.cinemas)
      })
    }
  },
  modules: {
  }
})

// vuex 默认将状态管理在内存, 刷新页面后会丢失, 需要异步状态数据共享管理
// vuex 在项目中的真正价值其实就是对后端数据的缓存快照, 减少重复数据请求, 减轻服务器压力, 提高用户体验。
