<template>
  <div class="swiper zdz" :class="name">
    <div class="swiper-wrapper">
      <slot></slot>
    </div>
  </div>
</template>

<script>
// REF: https://swiperjs.com/get-started
// import Swiper bundle with all modules installed
import Swiper from 'swiper/bundle'
// import styles bundle
import 'swiper/css/bundle'

export default {
  props: {
    preview: {
      type: Number,
      default: 1
    },
    name: {
      type: String,
      default: ''
    }
  },
  data() {
    return {}
  },
  mounted() {
    // 如果使用两次 .zdz 会导致 swiper 的 preview 属性冲突 (同一个组件使用两次， 但是使用不同的name属性)
    new Swiper('.' + this.name, {
      //   slidesPerView: 3.5,
      slidesPerView: this.preview, // 传递参数
      spaceBetween: 30,
      freeMode: true
    })
  }
}
</script>
