// 方法二: axios 自带方法, 自定义创建新实例
import axios from 'axios'
import { Toast } from 'vant';
// import vuex mapState

const http = axios.create({
    baseURL: `https://m.maizuo.com/gateway?pageNum=1&pageSize=10&type=1&k=1190794`,
    timeout: 1000,
    headers: {
        'X-Client-Info':
            '{"a":"3000","ch":"1002","v":"5.2.0","e":"1656764823272000278855681","bc":"110100"}',
    }
})


// axios 请求之前拦截 showLoading
http.interceptors.request.use(function (config) {
    // Do something before request is sent
    // console.log(config)
    Toast.loading({ message: '加载中...', forbidClick: true, duration: 4000 })
    return {
        ...config,
    }
}, function (error) {
    // Do something with request error
    return Promise.reject(error);
});


// axios 擦请求成功后拦截 hideLoading
http.interceptors.response.use(function (response) {
    // Any status code that lie within the range of 2xx cause this function to trigger
    // Do something with response data
    Toast.clear() // 加载结束, 关闭vant'加载中...'
    return {
        ...response,
    }
}, function (error) {
    // Any status codes that falls outside the range of 2xx cause this function to trigger
    // Do something with response error
    Toast.clear() // 加载结束(失败), 关闭vant'加载中...'
    return Promise.reject(error);
});

export default http