<template>
  <div class="city">
    <van-index-bar :index-list="indexList" @change="handleChange">
      <!-- 实现对JSON格式数据的遍历，并按照拼音分类 -->
      <div v-for="city in cityList" :key="city.type">
        <van-index-anchor :index="city.type" />
        <van-cell
          :title="item.name"
          v-for="item in city.list"
          :key="item.cityId"
          @click="handleCityClick(item)"
        />
      </div>
    </van-index-bar>
  </div>
</template>

<script>
import http from '@/util/http'
import { Toast } from 'vant'
// vuex import mapMutations
import { mapMutations } from 'vuex'
// import mixin object
import SHTAB from '@/util/shtab'

export default {
  // 混入 mixin
  mixins: [SHTAB],
  data() {
    return {
      cityList: []
      //   indexList: []
    }
  },
  computed: {
    indexList() {
      return this.cityList.map((item) => item.type)
    }
  },
  mounted() {
    http({
      url: 'https://m.maizuo.com/gateway?k=8772569',
      headers: {
        'X-Host': 'mall.film-ticket.city.list'
      }
    }).then((res) => {
      //   console.log(res.data.data.cities)
      //   数据转化成按照 a,b,c,.. 分组, 利用转化后的数组结合组件库进行渲染
      // city 数据给出了 pinyin, 可以根据拼音开头来进行分组
      this.cityList = this.renderCity(res.data.data.cities)
      //   获取 letter 分类不为零的存入 Index
      //   this.indexList = this.renderCity(res.data.data.cities).map(
      //     (item) => item.type
      //   )
    })
  },
  methods: {
    // mapMutations
    ...mapMutations(['hide', 'show']),
    // 处理点击城市
    handleCityClick(item) {
      // console.log(item.cityId, item.name)
      // 实现传统多页面跳转方案
      // location.href = '#/cinemas?cityname=' + item.name
      // 单页面跳转方案， 1. 需要中间人模式 (共有父组件都是App.vue)
      // 单页面跳转方案， 2. Bus事件总线 $on, $emit
      /**
       * 以上方法在管理太多页面跳转时会过于复杂
       */
      // 单页面跳转方案， 3. vuex, 因为, cityId 被多个业务页面所需要; 以及其他的公共状态需要被管理, 所以vuex才是最好的选择。
      // 修改城市名字的公共状态
      this.$store.commit('changeCityName', item.name)
      this.$store.commit('changeCityId', item.cityId)
      // 路由返回上一页
      this.$router.back()
    },
    // 处理分组变化的Toast轻提示
    handleChange(data) {
      // console.log('change', data)
      Toast({ duration: 100, message: data })
    },
    // 处理城市列表
    renderCity(cList) {
      console.log(cList)
      //   快速生成26个字母的数组, 65~91是A~Z的ASCII码
      var cityList = []
      var letterList = []
      for (var i = 65; i < 91; i++) {
        letterList.push(String.fromCharCode(i))
      }
      //   console.log(letterList)

      // 遍历letterList
      letterList.forEach((letter) => {
        // 按照拼音开头的letter进行过滤分组
        var newList = cList.filter(
          (city) => city.pinyin.substring(0, 1).toUpperCase() === letter
        )
        // console.log(newList)
        // 保证每个字母有对应的数据不为0
        if (newList.length > 0) {
          // 存入每个分组到最终的cityList中
          cityList.push({
            type: letter,
            list: newList
          })
        }
      })
      // console.log(cityList)

      return cityList
    }
  }
}
</script>

<style lang="scss">
.van-toast--html,
.van-toast--text {
  min-width: 30px;
}
</style>
