    <!-- van 组件库: 拉加载列表 -->
    <van-list
      v-model:loading="loading"
      :finished="finished"
      finished-text="我是有底线的"
      @load="onLoad"
      :immediate-check="false"
    >
      <van-cell
        v-for="item in filmlist"
        :key="item.filmId"
        v-on:click="hanidleFilmDesc(item.filmId)"
      >
        <img :src="item.poster" />
        <div class="filmTitle">
          {{ item.name }}
        </div>
        <div class="filmDesc">
          <!-- 解决没有评分的问题 -->
          <div :class="item.grade ? '' : 'hidden'">
            观众评分: <span style="color: coral">{{ item.grade }}</span>
          </div>
          <div class="filmActors">
            主演: <span>{{ item.actors | actorsFilter }}</span>
          </div>
          <div>
            国家: <span>{{ item.nation }} ｜ {{ item.runtime }}分钟</span>
          </div>
        </div>
      </van-cell>
    </van-list>
  </div>
</template>

<script>
// import axios from 'axios'
import Vue from 'vue'
import http from '@/util/http'
// 引入 van 组件库: list 懒加载
import { ref } from 'vue'
// vuex import mapState
import { mapState } from 'vuex'

Vue.filter('actorsFilter', (data) => {
  // 解决(undefined)没有主演的问题
  if (data === undefined) {
    return '暂无主演'
  }
  return data.map((item) => item.name).join(' ')
})

export default {
  // setup() {
  //   const list = ref([])
  //   const loading = ref(false)
  //   const finished = ref(false)
  //   const current = ref(1)
  //   const onLoad = () => {
  //     // 异步更新数据
  //     // setTimeout 仅做示例，真实场景中一般为 ajax 请求
  //     setTimeout(() => {
  //       for (let i = 0; i < 10; i++) {
  //         list.value.push(list.value.length + 1)
  //       }
  //       // 加载状态结束
  //       loading.value = false // 数据全部加载完成
  //       if (list.value.length >= 100) {
  //         finished.value = true
  //         // this.current++ // 当前页码加 1
  //       }
  //     }, 1000)
  //   }
  //   return {
  //     list,
  //     onLoad,
  //     loading,
  //     finished
  //   }
