  computed: mapState(['cityId']),
  data() {
    return {
      filmlist: [],
      loading: false,
      finished: false,
      current: 1,
      total: 0
    }
  },
  methods: {
    onLoad() {
      // 达到后端数据总长度的时候, 停止加载。
      // this.total !== 0 禁用懒加载
      if (this.filmlist.length === this.total && this.total !== 0) {
        this.finished = true
        return
      }
      this.current++ // 当前页码加 1
      http({
        url: `https://m.maizuo.com/gateway?cityId=${this.cityId}&pageNum=${this.current}&pageSize=10&type=1&k=1190794`,
        headers: {
          'X-Host': 'mall.film-ticket.film.list'
        }
      }).then((res) => {
        // console.log(res.data.data.films)
        // this.filmlist = res.data.data.films
        this.filmlist = [...this.filmlist, ...res.data.data.films]
        // loading 主动设置为 false
        this.loading = false
        console.log(this.filmlist)
      })
    },
    hanidleFilmDesc(id) {
      this.$router.push({
        name: 'zdzFilmDesc',
        params: {
          id
        }
      })
    }
  },
  mounted() {
    // 1. 需要封装
    // axios({
    //   url: 'https://m.maizuo.com/gateway?cityId=110100&pageNum=1&pageSize=30&type=1&k=1190794',
    //   headers: {
