    //   this.filmlist = res.data.data.films
    // })
    // 2. 方法一: 通过函数封装
    // http.httpForList().then((res) => {
    //   console.log(res.data)
    //   this.filmlist = res.data.data.films
    // })
    // 3. 方法二: axios 自带方法, 自定义创建新实例
    http({
      url: `https://m.maizuo.com/gateway?cityId=${this.cityId}&pageNum=${this.current}&pageSize=10&type=1&k=1190794`,
      headers: {
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then((res) => {
      this.filmlist = res.data.data.films
      this.total = res.data.data.total
    })
  }
}
</script>

