<template>
  <div>
    <!-- swiper -->
    <film-swiper :key="filmlist.length">
      <film-swiper-item v-for="item in filmlist" :key="item.filmId">
        <div class="img swiper-zoom-container"><img :src="item.poster" /></div>
        <!-- <img :src="item.poster" /> -->
      </film-swiper-item>
    </film-swiper>
    <!-- <div style="height: 200px; background: yellow">swiper</div> -->

    <!-- <div>二级声明式导航</div> -->
    <!-- 解决二级声明式导航的洗顶 -->
    <film-header class="sticky"></film-header>
    <router-view></router-view>
  </div>
</template>

<script>
// 通过 webpack 导入 CSS 文件

import filmSwiper from '@/components/films/FilmSwiper.vue'
import filmSwiperItem from '@/components/films/FilmSwiperItem.vue'
import filmHeader from '@/components/films/FilmHeader.vue'
import axios from 'axios'
// vuex
import { mapState } from 'vuex'

export default {
  components: {
    filmSwiper,
    filmSwiperItem,
    filmHeader
  },
  computed: {
    ...mapState(['cityId'])
  },
  data() {
    return {
      filmlist: []
    }
  },
  mounted() {
    axios({
      url: `https://m.maizuo.com/gateway?cityId=${this.cityId}&pageNum=1&pageSize=10&type=1&k=1190794`,
      headers: {
        'X-Client-Info':
          '{"a":"3000","ch":"1002","v":"5.2.0","e":"1656764823272000278855681","bc":"110100"}',
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then((res) => {
      // console.log(res.data)
      this.filmlist = res.data.data.films
    })
  }
}
</script>

<style lang="scss">
.navactive {
  color: green;
}

img {
  width: 25%;
}
.sticky {
  position: sticky; // 粘性定位, (共5个值)除了 sticky 之外还有: staic, react, absolute, fixed, relative
  top: 0px;
  background: white;
  z-index: 100;
}
</style>
